body {
  font-family: 'Montserrat', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #1b1c1d;
  min-height: 100vh;
  display: block;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.buffer{
  margin-top: 4vw;
}

.ui.container {
  display: flex;
  max-width: 100% !important;
}

.bg {
  /* The image used */
  background-image: url("../src/img/sunrise.jpg");
  box-shadow: inset 0 0 0 1000px #00000069;

  /* Full height */
  height: 100%; 

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 2vw;
}

/* logo */
.logo {
  border-style: none;
  padding-top: 1.5vw;
  padding-left: 1.5vw;
}

/* nav bar */

.head {
  position: fixed;
  background-color: #1b1c1d;
  width: 100% !important;
  z-index: 100;
}

.ui.menu {
  font-size: 1rem;
  padding-left: 10vw;
  padding-right: 20vw;
}

.ui.secondary.pointing.menu .item {
  font-size: large;
  color: #9e9e9ec4;
}

.ui.secondary.pointing.menu .active.item {
  background-color: transparent;
  -webkit-box-shadow: none;
  box-shadow: none;
  font-weight: 800;
  font-size: large;
  border-bottom-color: white;
  color: rgb(255 255 255);
}

/* search input */
.ui.transparent.input>input {
  background-color: #00000078!important;
  padding: 1vw !important;
  border-radius: 2vw !important;
}

.ui.inverted.segment {
  margin-top: 0.5vw;
}

.ui.transparent.icon.input>input {
  padding-left: 2vw !important;
  padding-right: 4vw !important;
}

/* search icon */
.ui.transparent.icon.input>i.icon {
  width: 3.1em;
}

/* home */
.ui[class*="left aligned"].container {
  text-align: left;
  padding-top: 5vw;
  margin-left: 0 !important;
  padding-left: 2vw !important;
}

.social {
  display: grid;
  padding: 1vw;
  margin: 1vw;
}

.ico {
  padding: 1vw;
  border: 4px solid aliceblue;
  border-radius: 5vw;
  color: aliceblue;
  margin-bottom: 2vw;
}

.content {
  font-family: cursive;
  padding: 4vw;
  line-height: 10vw;
  margin-top: 3vw;
  line-break: auto;
  border-inline-end: inherit;
}

.ui.button {
  font-size: 1.5rem;
  border-radius: 5vw;
}

.astronaut{
  display: block;
  margin-right: -30vw;
}

@keyframes swing {
  0%, 100% { transform: rotate(30deg); }
  20% {transform:scale(.95);}
  50% { transform: rotate(30deg); }
  80% {transform:scale(.95);}
}

#astro {
  animation: swing 10s infinite ease-in-out;
}

.event{
  margin:50px auto;
  text-align:center;
}

.strip{
  display: inline-flex;
  padding: 3vw;
  padding-left: 20vw;
  width: 100%;
  margin-top: -1vw;
  padding-top: 7vw;
  background-color: teal;
}

.join {
  margin-left: 50% !important;
}

.buffer2 {
  margin-top: -40vw;
}

/* ================================================== */
.left-block {
  width: 100%;
  padding-top: 10vw;
  padding: 5vw;
  font-size: large;
  text-align: initial;
}

.right-block{
  width: 100%;
  padding-top: 10vw;
  padding: 5vw;
  font-size: large;
  text-align: initial;
}

.smoke {
  width: max-content;
  margin-left: -12.5vw;
  padding-top: 22vw;
}

section.rocket {
  display: grid;
  grid-template-columns: 1fr 100px 1fr;
  grid-gap: 80px;
  padding: 3vw 0;
}

section.rocket .rocket-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 6vw;
}

section.rocket .rocket-body span {
  font-size: 0;
  position: relative;
  z-index: 1;
}

section.rocket .rocket-body span img {
  width: 100%;
}

section.rocket .block {
  width: 70%;
}

section.rocket .block h1 {
  font-size: 32px;
  font-weight: 400;
}

hr {
  box-sizing: content-box;
  height: 0px;
  width: 24vw;
  overflow: visible;
  display: block;
  position: absolute;
  padding-left: 2vw;
  margin-left: 15vw;
}

.abtHr {
  box-sizing: content-box;
  height: 0px;
  width: 8vw;
  overflow: visible;
  display: block;
  position: absolute;
  margin-left: -17vw;
  margin-top: -3vw;
}

.payloadHr {
    box-sizing: content-box;
    height: 0px;
    width: 5vw;
    overflow: visible;
    display: block;
    position: absolute;
    margin-left: -9vw;
    margin-top: -2vw;
}

.dragonHr {
  box-sizing: content-box;
  height: 0px;
  overflow: visible;
  display: block;
  position: absolute;
  padding-left: 0vw;
  width: 15vw;
  margin-left: 25vw;
  margin-top: -3vw;
}

section.rocket .block .with-underline {
  position: relative;
  margin-bottom: 38px;
}

section.rocket .block .with-underline:before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -12px;
  display: block;
  width: 38%;
  height: 2px;
  background: var(--pure);
}

section.rocket .block h1.with-underline:before {
  width: 0%;
  /* width: 20%; */
}

section.rocket p {
  line-height: 1.6;
}

section.rocket .block h2 {
  font-weight: 400;
  margin-bottom: 20px;
}

section.rocket .block.payload {
  padding-top: 130px;
}

section.rocket .block.payload h2 {
  padding-left: 0;
  /* padding-left: 80px; */
}

section.rocket .block.dragon {
  padding-top: 120px;
}

section.rocket .block button {
  margin-top: 30px;
}

section.rocket .block.composite {
  padding-top: 60px;
}

section.rocket .block.first-stage {
  padding-top: 60px;
}

section.rocket .block.capacity {
  display: flex;
  align-items: center;
  padding-top: 40px;
}

section.rocket .block.capacity > div:first-child {
  margin-right: 40px;
}

section.rocket .block.capacity h4 {
  font-size: 19px;
  font-weight: 400;
  margin-bottom: 20px;
}

.blockEng {
  margin-top: -6px;
}

section.rocket .block.capacity h6 {
  font-size: 40px;
  font-weight: 400;
}

section.rocket .block.capacity h6 small {
  font-size: 12px;
}

section.rocket .block.second-stage {
  padding-top: 60px;
}

section.rocket .right-block .block.capacity {
  padding-top: 120px;
}

section.rocket .right-block .block.capacity.single {
  padding-top: 40px;
}

section.rocket .right-block .block.engines {
  display: flex;
  justify-content: center;
  padding-top: 40px;
}

section.rocket .right-block .block.engines img {
  width: 85%;
}

section.rocket .right-block .block.marine {
  padding-top: 100px;
  width: 70%;
}

section.rocket .right-block .block.marine h2 {
  font-size: 14px;
  padding-left: 100px;
  margin-bottom: 26px;
}

section.rocket .right-block .floating-graphics {
  position: sticky;
  float: right;
  top: 60px;
  margin-right: -80px;
}

section.rocket .right-block .floating-graphics img {
  height: 350px;
}

section.rocket .rocket-body span:after, section.rocket .rocket-body span:nth-child(3):before {
  content: '';
  display: block;
  position: absolute;
  width: 0;
  height: 2px;
  background: var(--pure);
  transition: width 1s ease-in-out;
}

section.rocket .rocket-body span:nth-child(1):after {
  bottom: 16px;
  left: 96%;
}

section.rocket .rocket-body span:nth-child(2):after {
  bottom: 62%;
  right: 100%;
}

section.rocket .rocket-body span:nth-child(3):after {
  bottom: 64%;
  right: 90%;
}

section.rocket .rocket-body span:nth-child(3):before {
  bottom: 36%;
  left: 90%;
}

section.rocket .rocket-body span.active:nth-child(1):after {
  width: 120px;
}

section.rocket .rocket-body span.active:nth-child(2):after {
  width: 180px;
}

section.rocket .rocket-body span.active:nth-child(3):after {
  width: 200px;
}

section.rocket .rocket-body span.active:nth-child(3):before {
  width: 160px;
}

@keyframes jump {
  50% {
      transform: translateY(-50px);
  }
}

section.rocket .rocket-body span.active:nth-child(1) {
  animation: jump 1s ease-in-out forwards;
}



/* Footer -> Career */
footer {
  background: var(--dark);
  padding: 60px 0;
}

footer > div {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

footer .copyright span {
  font-size: 15px;
  display: inline-block;
  margin-top: 5px;
}

footer .copyright img {
  height: 30px;
}

footer h1 {
  font-size: 2rem;
  position: relative;
  margin-bottom: 2vw;
   width: fit-content;
}

footer h1:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -8px;
  display: block;
  width: 60px;
  height: 2px;
  background: var(--pure);
}

footer ul {
  list-style-type: none;
}

footer ul li:nth-child(even) {
  margin: 10px 0;
}

footer ul li a {
  color: var(--mild);
  text-decoration: none;
  font-size: 1vw;
  display: flex;
  margin-left: -2vw;
  padding-bottom: 0.5vw;
  transition: all .3s ease-in-out;
}

footer ul li a:hover {
  color: var(--pure);
}

footer .footer-socials a {
  color: var(--mild);
  text-decoration: none;
  font-size: 1.5vw;
  display: flex;
  margin-left: -2vw;
  padding-bottom: 1vw;
  transition: all .3s ease-in-out;
}

footer .footer-socials a:hover {
  color: var(--pure);
}

:root {
  --primary: #1E1E1E;
  --dark: #121212;
  --pure: #ffffff;
  --gray: #1d1d1d;
  --mild: #b9b8b8;
}
